} /* Literal.String.Symbol */
.highlight .bp {
	color: var(--code-color-13);
} /* Name.Builtin.Pseudo */
.highlight .vc {
	color: var(--code-color-7);
} /* Name.Variable.Class */
.highlight .vg {
	color: var(--code-color-7);
} /* Name.Variable.Global */
.highlight .vi {
	color: var(--code-color-7);
} /* Name.Variable.Instance */
.highlight .il {
	color: var(--code-color-14);
} /* Literal.Number.Integer.Long */
